import axios from 'axios';
import Constantes from '../../store/constantes';

const API_PAGOS = Constantes.API_PAGOS;
const MODULORENTAS = Constantes.MODULO_TRIBUTO_RENTAS;
const API_FILES_ANULA = Constantes.filesAnula;

let inicioPago = (pagoRequest) => {
    return axios.post(`${API_PAGOS}/inicio-pago`, pagoRequest)
}
let infoPago = (numeroPedido) => {
    return axios.get(`${API_PAGOS}/info-pago/${MODULORENTAS}/${numeroPedido}`)
}
let eliminarRecibo = (data) => {
    return axios.get(`${API_FILES_ANULA}`, data);    
}

export{
    inicioPago,
    infoPago,
    eliminarRecibo
}


