<template>
  <div class="wizard-adjuntos" v-if="loadButtonPayment && dataPago">
    <div class="container">
      <div class="card card-muni card-muni-medi">
        <h3>PROCESO DE PAGO</h3>
          <div class="product">
            <h5>{{dataPago.nombreTipoTramite}}</h5>
            <div class="row mt-4 mb-4">
              <div class="col-md-4 text-center">
                <div class="view">
                  <p>Nmero de operacin</p>
                  <b>{{dataPago.numeroPedido}}</b>
                </div>
              </div>
              <div class="col-md-4 text-center">
                <div class="import">
                  <p>Importe a pagar</p>
                  <b>S/. {{dataPago.precioTramite | formatoMoneda}}</b>
                </div>
              </div>
              <div class="col-md-4 text-center">
                <div class="view">
                  <p>Fecha</p>
                  <b>{{fechaActualx}}</b>
                </div>
              </div>
              <div class="col-md-12 text-center mt-2">
                <button-payment v-if="dataPago" :dataPago="dataPago"></button-payment>
              </div>
            </div>            
          </div>
          <div class="row">
            <div class="col-xs-12 col-sm-12 col-md-12 text-center d-none d-sm-block">
              <button class="btn btn-primary" @click="cancelarProceso()">Cancelar proceso</button>
              <button class="btn btn-success" @click="regresarPagina()">Regresar</button>
            </div>
          </div>
        </div>
    </div>
  </div>
</template>
<script>
import ButtonPayment from '../commons/buttonPayment'
import Constantes from '../../store/constantes'
import EventBus from '../../helpers/EventBus'
import Util from '../../tools/util'
import UtilNumerico from '../../tools/utilNumerico'

import {infoPago} from '../services/pagosService'
window.onhashchange = function() {
  location.reload();
}
export default {
  components:{ButtonPayment},
    data(){
      return {
        app:{
          name: "appRentas", 
          data: null
        },
        respuestaPago: "",
        loadButtonPayment: false,
        dataPago:{}
      }
    },
    beforeDestroy() {
      this.respuestaPago = ''
      this.loadButtonPayment = false
      this.dataPago = {}
    },
    created(){
      this.app.data = Util.storage.get(this.app.name);
    },
    mounted(){
      EventBus.$emit("loadershow", true);

      let respuestaPago = Constantes.respuestaPagoTributos;

      this.dataPago.numeroPedido = this.$route.params.numeroPedido;
      this.dataPago.respuestaPago = `${respuestaPago}?numeroPedido=${this.dataPago.numeroPedido}`;
      
      this.iniciaProceso();
    },
    methods:{
      iniciaProceso(){
        infoPago(this.dataPago.numeroPedido).then((response)=>{
          if(response.data.success){
            let data = response.data.data;

            this.dataPago.precioTramite = data.desMonto;
            this.dataPago.nombreTipoTramite = data.desPagoDesc;

            this.loadButtonPayment = true;

            EventBus.$emit("loadershow", false);
          }
        }).catch(e => console.log(e));
      },
      regresarPagina(){
          Util.pagina.go(this, "/pago-tributo/contribuyente/estado-cuenta");
      },
      cancelarProceso(){
          delete this.app.data.cuenta;
          delete this.app.data.siContribuyentesAsociados;

          Util.storage.set(this.app.name, this.app.data);

          Util.pagina.go(this, "/");
      }
    },
    filters:{
      formatoMoneda(monto){
        return UtilNumerico.formatoMoneda(monto);
      }
    },
    computed: {
    fechaActualx() {
      let fc = this.fecTramite;
      if (fc == null) {
        fc = new Date();
      }
      return (
        fc.getUTCDate() +
        "/" +
        (fc.getUTCMonth() + 1) +
        "/" +
        fc.getUTCFullYear()
      );
    }
  }
}
</script>
<style lang="scss">
.product {
    border: dashed 1px #50cef5;
    border-radius: 10px;
    padding: 15px;
    margin-bottom: 15px;
    color: #696969;
    .text-resum {
      font-size: 13.5px;
      max-width: 535px;
      text-align: center;
      padding-bottom: 30px;
      margin: auto;
      text-transform: inherit;
    }
    p {
      margin-bottom: 0px;
    }
    > p {
      padding-bottom: 10px;
      text-transform: capitalize;
    }
    &.final {
      > div {
        text-align: center;
        img {
          max-width: 73px;
          position: relative;
          width: 100%;
          padding-bottom: 17px;
          padding-top: 15px;
        }
      }
      .pagolink {
        text-align: center;
        margin-bottom: 10px;
      }
      a {
        color: #21c0f3;
        text-decoration: underline;
      }
      .tet {
        font-size: 16px;
        text-align: center;
        color: #21c0f3;
        font-weight: bold;
        padding-bottom: 12px;
      }
      .text-fin {
        padding-top: 12px;
      }
    }
}
</style>


