<template>
    <div>
        <div class="col-md-12 text-center mt-4">
            <div class="form-check">
            <b-form-checkbox
                id="chkCondiciones"
                inline
                value="activado"
                unchecked-value="desactivado"
                v-model="chkCondiciones">
                Acepto los <a href="javascript:;" class="text-primary">trminos y condiciones</a>
            </b-form-checkbox><br/>
            <label class="form-check-label" for="chkCondiciones">
                <small><i>Para realizar pagos debe aceptar los trminos y condiciones</i></small>
            </label>
            </div>
        </div>
        <div class="mt-2" id="layoutFormPayment">
            <form id="formPago" v-if="dataPago" :action="dataPago.respuestaPago"></form>
        </div>
    </div>
</template>
<script>
import EventBus from '../../helpers/EventBus'
import Constantes from '../../store/constantes'
import Vue from "vue";

import {inicioPago} from '../services/pagosService'

export default {
    props:["dataPago"],
    data(){
      return {
        chkCondiciones: ""
      }
    },
    created(){
      this.inicioPago();

      
    },
    methods: {
      inicioPago(){
        console.log('entra')
        var pagoRequest = {};
        pagoRequest.nroOrdenPago = this.dataPago.numeroPedido;
        pagoRequest.monto = this.dataPago.precioTramite;
        pagoRequest.modulo = Constantes.MODULO_TRIBUTO_RENTAS;

        EventBus.$emit("loadershow", true);

        inicioPago(pagoRequest).then((response) => {
            if(response.data.success){
                let data = response.data.params;

                this.mostrarVisa=true;
                this.mostrarContinuar=false;
                
                let scriptForm = document.createElement("script");
                scriptForm.setAttribute("src", data.dataSrc);
                scriptForm.setAttribute("data-sessiontoken", data.dataSessiontoken);
                scriptForm.setAttribute("data-channel", data.dataChannel);
                scriptForm.setAttribute("data-merchantid", data.dataMerchantid);
                scriptForm.setAttribute("data-merchantlogo", data.dataMerchantlogo);
                scriptForm.setAttribute("data-purchasenumber", data.dataPurchasenumber);
                scriptForm.setAttribute("data-amount", data.dataAmount);
                scriptForm.setAttribute("data-expirationminutes", data.dataExpirationminutes);
                scriptForm.setAttribute("data-timeouturl", data.dataTimeouturl);

                let form = document.getElementById("formPago");
                form.appendChild(scriptForm);
                
                setTimeout(() => { 
                    EventBus.$emit("loadershow", false);

                    this.desactivarBoton();

                    let boton = document.getElementsByClassName("start-js-btn modal-opener default");
                    boton[0].addEventListener("click", function(){
                      EventBus.$emit("loadershow", true);
                    });

                    setInterval(function(){
                        let css = document.getElementById("visaNetJS").style.display;
                        if(css=="none"){
                            EventBus.$emit("loadershow", false);
                        }
                    }, 1000);

                }, 1000);
            }else{
              this.$swal({
                      icon: "info",
                      title: "Aviso",
                      text:
                        "Tenemos problemas de comunicacin con la plataforma de pagos NIUBIZ. Favor vuelva a intentar en unos minutos."
                    });
                    EventBus.$emit("loadershow", false);
            }
        }).catch(e => console.log(e));
      },
      activarBoton(){
        let boton = document.getElementsByClassName("start-js-btn modal-opener default");
        if(boton.length==0){
          // this.cargaBotonPago();
        }else{
          boton[0].removeAttribute("disabled");
          boton[0].style.cursor = "pointer";
        }
      },
      desactivarBoton(){
        let boton = document.getElementsByClassName("start-js-btn modal-opener default");
        if(boton.length==0){
          // this.cargaBotonPago();
        }else{
            boton[0].setAttribute("disabled", true);
            boton[0].style.cursor = "not-allowed";
        }
      }
    },
    watch:{
      chkCondiciones(value){
        if(value==="activado"){
            this.activarBoton();
        }else{
            this.desactivarBoton();
        }
      }
    }
}
</script>


